const routes = [
  {
    path:'/',
    component: store.state.isAuth?(HomePage):(LoginPage)
  },
  {
    path:'/sign-up',
    component: SignUpPage
  },
  {
    path:'/otp',
    component: OTPPage
  },

  {
    path:'/main-slider',
    component: FullScreenSliderPage
  },
  // {
  //   path:'/:catchAll(.*)',
  //   component: store.state.isAuth?(HomePage):(LoginPage)
  // },
  {
    path:'/:catchAll(.*)',
    component: FullScreenSliderPage
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
